body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #ECF9FF;
}

.App {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App__title {
  border-radius: 20px 20px 0 0;
  width: 1024px;
  text-align: center;
  padding: 10px 0 20px;
  color: #F9F5E7;
  background-color: #A7727D;
  text-shadow: 2px 2px black;
}

.App__footer {
  text-align: center;
  padding: 10px;
  width: 1024px;
  background-color: #A7727D;
  color: #F9F5E7;
  text-shadow: 1px 1px black;
  border-radius: 0 0 10px 10px;
}
.App__form {
  width: 1024px;
  background-color: #FFE7CC;
  display: flex;
  flex-direction: column;
  font-size: 20px;
}


input {
  padding: 5px 10px;
  font-size: 20px
}

.App__form-text {
  display: flex;
}

.App__form-text input {
  margin: 12px;
  width: 1000px;
  border-radius: 10px;

}

.App__form-priority {
  display: flex;
  justify-content: center;
}

.App__form-priority__date {
  margin: 20px;
}

.App__form-priority__date input {
  border-radius: 10px;
}

.App__form-priority__importance {
  margin: 20px;
}


.App__form button {
  padding: 10px 10px;
  /* text-transform: uppercase; */
  cursor: pointer;
  margin: 0 20px 20px 20px;
  font-size: 20px;
  color: #F9F5E7;
  background-color: #A7727D;
  border-color: #F9F5E7;
  border-radius: 15px;
  transition: 0.3s;

  text-shadow: 1px 1px black;
}

.App__form button:hover {
  background-color: #E4D0D0;
  color: #A7727D
}

.App__form button:active {

  background-color: #A7727D;
  color: black
}
.priority {
    font-weight: bold;
}

.toLate {
    color: red;
}

li {
    list-style: none;
}

.App__task-list__task-buttons {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.App__task-list__task button {
    height: 30px;
    margin: 3px;
    border-radius: 5px;
    cursor: pointer;
    border-width: 0px;
    display: block;
    transition: 0.3s;
}

.App__task-list__done-button {
    background-color: #939B62;
    color: white;
    font-weight: bold;
    line-height: 30px;
}

.App__task-list__done-button:hover {
    background-color: #79804f;
}

.App__task-list__done-button:active {
    background-color: #939B62;
    color: #79804f;
}

.App__task-list__delete-button {
    width: 30px;
    font-size: 30px;
    line-height: 30px;
    background-color: #FF7B54;
    color: white;
    font-weight: bold
}

.App__task-list__delete-button:hover {

    background-color: #dd5b34;
}

.App__task-list__delete-button:active {
    background-color: #FF7B54;
    color: #dd5b34;
}
.App__task-list {
    background-color: #FFE7CC;
    width: 1024px;
    border-top: 2px #A7727D dashed
}

.App__task-list__header {
    text-align: center;
    padding: 10px;
    background-color: #F9F5E7;
    cursor: pointer;
}

.App__task-list__header:hover {
    opacity: 0.7;
}

.App__task-list ul {
    padding: 0;
}

.App__task-list ul li {

    padding: 10px 15px;
    display: flex;
}

.App__task-list ul li:nth-child(even) {
    background-color: #F9F5E7;
}

.App__task-list ul li .left {
    width: 80%;
}

.App__task-list ul li .right {
    text-align: right;
    width: 20%;
}
.App__done-tasks__task .App__done-tasks__task-buttons {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.App__done-tasks__task button {
    height: 30px;
    margin: 3px;
    border-radius: 5px;
    cursor: pointer;
    border-width: 0px;
    display: block;
    transition: 0.3s;
}

.App__done-tasks__delete-button {
    width: 30px;
    font-size: 30px;
    line-height: 30px;
    background-color: #FF7B54;
    color: white;
    font-weight: bold
}

.App__done-tasks__delete-button:hover {

    background-color: #dd5b34;
}

.App__done-tasks__delete-button:active {
    background-color: #FF7B54;
    color: #dd5b34;
}

.App__done-tasks__back-button {
    background-color: #fdc135;
    color: white;
    font-weight: bold;
    line-height: 30px;
}
.App__done-tasks {
    background-color: #F9F5E7;
    width: 1024px;
    border-top: 2px #A7727D dashed
}

.App__done-tasks__header {
    background-color: #EDDBC7;
    padding: 10px;
    text-align: center;
    cursor: pointer;
}

.App__done-tasks__header:hover {
    opacity: 0.7;
}

.App__done-tasks ul {
    padding: 0;
}

.App__done-tasks ul li {

    padding: 10px 15px;
    display: flex;
}

.App__done-tasks ul li:nth-child(even) {
    background-color: #FFE7CC;
}

.App__done-tasks ul li .left {
    width: 80%;
}

.App__done-tasks ul li .right {
    text-align: right;
    font-size: 12px;
    width: 20%;
}
